___CSS_LOADER_EXPORT___.push([module.id, `body {
    margin: 0px;
    height: 100vh;
    display: grid;
    grid-template-rows: 2fr 8fr;
    color: white;
    text-align: center;
}

.header {
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4rem;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search {
    width: 500px;
    margin: 50px 0px;
}

form {
    display: flex;
    gap: 10px;
}

input {
    flex-grow: 1;
}

.card {
    box-sizing: border-box;
    background-color: black;
    width: 500px;
    font-size: 1.5rem;
    padding: 30px;
    flex-direction: column;
    gap: 20px;
    display: flex;
}

.city , .temp {
    font-size: 2.5rem;
    font-weight: bold;
}

.temp {
    display: flex;
    justify-content: center;
    gap: 15px;
}

.loading {
    width: 500px;
    height: 313px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: 500px 313px;
    background-color: black;
}

.displayNone{
    display: none;
}
