import "./style.css"

async function getData(location) {
    try {
        let baseUrl = "https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/";
        let url = `${baseUrl}${location}?key=CGCAWWNLHDKLDFCJ5ZVFH99W9`;
        let response = await fetch(url);
        console.log("The response is ", response);
        let data = await response.json();
        console.log("The data is ", data);
        return data;
    } catch (error) {
        console.error(error);
        return undefined;
    }
}

function processData(data) {
    if (data === undefined) {
        return undefined;
    }
    let weather = {};
    let fullWeather = data.days[0];
    weather.address = data.address;
    weather.date = fullWeather.datetime;
    weather.condition = fullWeather.conditions;
    weather.tempmax = fullWeather.tempmax;
    weather.tempmin = fullWeather.tempmin;
    weather.humidity = fullWeather.humidity;
    return weather;
}

function getAndProcessData(location) {
    return getData(location)
        .then((data) => processData(data))
        .then((weather) => { console.log(weather); return weather; });
}

function getCelsius(fahrenheit) {
    let celsius = (fahrenheit - 32) * 5 / 9
    return celsius.toPrecision(3) + "&deg;C";
}


function main() {
    const form = document.querySelector("form");
    const input = document.querySelector("input");
    const button = document.querySelector(".change");
    const card = document.querySelector(".card");
    const loading = document.querySelector(".loading");
    const cityDiv = document.querySelector(".city");
    const dateDiv = document.querySelector(".date");
    const minTempDiv = document.querySelector(".min-temp");
    const maxTempDiv = document.querySelector(".max-temp");
    const conditionDiv = document.querySelector(".condition");
    const humidityDiv = document.querySelector(".humidity");

    let isCelsius = true;
    let mainWeather = undefined;

    function changeDisplay(){
        card.classList.toggle("displayNone");
        loading.classList.toggle("displayNone");
    }

    function displayData(weather) {
        if (weather === undefined) {
            alert("The input is invalid, please enter an valid city");
            form.reset();
            return;
        }
        mainWeather = weather;
        cityDiv.textContent = weather.address;
        dateDiv.textContent = weather.date;
        minTempDiv.innerHTML = isCelsius ? getCelsius(weather.tempmin) : weather.tempmin + "&deg;F";
        maxTempDiv.innerHTML = isCelsius ? getCelsius(weather.tempmax) : weather.tempmax + "&deg;F";
        conditionDiv.textContent = weather.condition;
        humidityDiv.textContent = `Humidity : ${weather.humidity}%`;
    }

    function formHandler(e) {
        e.preventDefault();
        changeDisplay();
        getAndProcessData(input.value)
            .then((weather) => {displayData(weather); changeDisplay();});
    }
    form.addEventListener("submit", formHandler);

    function displayTemp(){
        button.textContent = isCelsius ? "Use C" : "Use F";
        isCelsius = !isCelsius;
        if(mainWeather === undefined) return;
        minTempDiv.innerHTML = isCelsius ? getCelsius(mainWeather.tempmin) : mainWeather.tempmin + "&deg;F";
        maxTempDiv.innerHTML = isCelsius ? getCelsius(mainWeather.tempmax) : mainWeather.tempmax + "&deg;F";
    }
    button.addEventListener("click", displayTemp);
}


main();

